
import IgUserInfo from "../baseUI/IgUseInfo.vue";
import Members from "./members.vue";
import Matches from "./matches.vue";
import { defineComponent } from "vue";
export default defineComponent({
  // el: "#tabs",
  components: {
    IgUserInfo,
    Members,
    Matches,
  },
  data() {
    return {
      adminMsg1: false,
      adminMsg2: false,
      showJoinDialog: false,
      showLeaveDialog: false,
      showRequestSentBtn: false,
      showInviteBtn: false,
      selectedTab: "members",
      matchesTab: false,
      matcheswidth: false,
    };
  },
  methods: {
    selectTab(key) {
      this.selectedTab = key;
      if (this.selectedTab == "matches") {
        this.matcheswidth = false;
      } else {
        this.matcheswidth = true;
      }
    },
    closebtn() {
      this.showJoinDialog = false;
      this.matchesTab = false;
    },
  },
});
