import Participants from "@/components/tournament/participants.vue";
import { mapGetters } from "vuex";
import { Dialog } from "@headlessui/vue";
import Share from "@/components/baseUI/Share.vue";
export default defineComponent({
  data() {
    return {
      selectedTab: "overview",
      showJoinDialog: false,
      showLeaveDialog: false,
      showRequestSentBtn: false,
      showInviteBtn: false,

      friendData: [
        { img: "", name: "Gaurav Moradiya", live: false, selected: false },
        { img: "", name: "Jay Kukadiya", live: true, selected: true },
        { img: "", name: "Danish Kukadiya", live: false, selected: false },
        { img: "", name: "Indarani Shree", live: false, selected: false },
        { img: "", name: "Nayah Tantoh", live: false, selected: false },
