        { img: "", name: "Danish Kukadiya", live: false, selected: false },
        { img: "", name: "Indarani Shree", live: false, selected: false },
      ],
    };
  },
  components: {
    OverView,
    BracketsMatches,
    GroupStages,
    Participants,
    Dialog,
    Share,
  },
  methods: {
    changeTab(key: string) {
      this.selectedTab = key;
    },
    sendRequest() {
      this.showRequestSentBtn = true;
      this.showInviteBtn = true;
      this.showJoinDialog = false;
    },
    leavTeam() {
      this.showInviteBtn = false;
      this.showRequestSentBtn = false;
      this.showLeaveDialog = false;
    },
  },
  computed: {
    ...mapGetters(["loggedIn"]),
  },
});
</script>
